import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"
import "./article.scss"
import { Link } from "gatsby"
import _ from "lodash"
const ArticleList = ({ articleList = [] }) => {
  const { allMarkdownRemark } = useStaticQuery(
    graphql`
      query {
        allMarkdownRemark(
          filter: { fileAbsolutePath: { regex: "/articles/" } }
          sort: { fields: frontmatter___date, order: DESC }
        ) {
          edges {
            node {
              fileAbsolutePath
              id
              html
              frontmatter {
                date(formatString: "MMMM DD, YYYY")
                title
              }
            }
          }
        }
      }
    `
  )
  return (
    <div className="article-wrap">
      <header>The Latest</header>
      <div className="articles">
        {allMarkdownRemark.edges &&
          allMarkdownRemark.edges.map(item => {
            return (
              <Link
                to={"/article/" + item.node.id}
                className="article-item"
                key={item.node.id}
              >
                <div className="title">{item.node.frontmatter.title}</div>
                <div
                  className="content"
                  dangerouslySetInnerHTML={{
                    __html: _.truncate(item.node.html, {
                      length: 150,
                      omission: " [...]",
                    }),
                  }}
                ></div>
                <div className="date">{item.node.frontmatter.date}</div>
              </Link>
            )
          })}
      </div>
    </div>
  )
}

export default ArticleList

ArticleList.defaultProps = {
  articleList: [],
}
